import tButton from '../../../components/twoButton/twoButton.vue'
import infoImg from '../../../components/m-info-img/m-info-img'
import infoText from '../../../components/m-info-text/m-info-text'
export default {
	data() {
		return {
			btn: {
				type: 'primary',
				con2: '确认',
				dsiabled: false
			},
			invoice: {
				company: 'xxxxxxxx公司',
				disabled: true,
				moeny: '111111111',
				placeholder: '22222222',
				company: '开票公司',
				moeny: '开票金额',
				types: '发票类型',
				way: '拆分方式',
				currentOil: '当前购油量',
				surplus: '剩余未拆油量',
			},
			list: [], //拆分次数集合
			ids: -1, //默认不需要发票
			yesORno: '是',
			typeInvoice: '请选择发票类型',
			invoiceTypes: false,
			id: '',
			invoiceNum: [], //拆分数量
			show: false, //发票开关，默认关
			value: false,
			num: 0, //第一个拆分数量
			currentOil: '', //当前购油数量
			move: false,
			no: '',
			company: '',
			status: '',
			moeny: '',
		}
	},
	onLoad(option) {
		this.id = option.id; //id
		this.currentOil = option.number; //油量
		this.no = option.no; //订单编号
		this.company = option.company; //购油公司
		this.status = option.status; //状态
		this.moeny = option.moeny; //油价
	},
	methods: {
		// 点击选择发票类型
		chooseInvoice() {
			this.invoiceTypes = !this.invoiceTypes
		},
		// 选择发票类型
		invoiceType(e) {
			console.log(e)
			this.typeInvoice = e.target.id;
			this.invoiceTypes = !this.invoiceTypes
		},
		// 点击取消选择
		chooseUsersShow() {
			this.invoiceTypes = !this.invoiceTypes
		},
		// 展开发票  
		showIncoice(e) {
			if (e.target.value) {
				this.yesORno = "是"
				this.ids = 1; //需要发票
			} else {
				this.yesORno = "否"
				this.ids = -1
			}
			this.show = !this.show;
		},
		// 点击加号添加一行
		add() {
			this.list.push({
				nums: ''
			})
		},
		// 点击减号删除一行
		detal(val) {
			this.list.splice(val, 1)
		},
		// 不需要发票
		cancelOrder() {
			uni.showModal({
				title: '提示',
				content: '确认不开发票吗?',
				success: (res) => {
					if (res.confirm) {
						this.test.post('order/make_invoice', {
							id: this.id,
							is_invoice: '否',
							invoice_money: this.moeny
						}).then(res => {
							console.log(res)
							if (res.statusCode == 200 && res.data.errorCode == 0) {
								uni.redirectTo({
									url: '../orderDtails/orderDtails?id=' + this.id + '&no=' + this.no + '&status=' + this.status
								})
							} else {
								uni.showToast({
									title: res.data.message,
									icon: 'none'
								});
							}
						}).catch(err => {
							console.log(err)
						})
					} else {
						return;
					}
				}
			})

		},
		// 确认开票
		invoiceSure(invoiceAll) {

			if (this.typeInvoice == "请选择发票类型") {
				uni.showToast({
					title: '请选择发票类型',
					icon: 'none'
				});
			} else {
				this.invoiceNum = [];
				const that = this;
				if (this.list.length == 0) {
					this.invoiceNum.push(this.num)
					this.invoiceNum = String(this.invoiceNum)
				} else {
					this.invoiceNum.push(this.num)

					this.list.forEach(el => {
						console.log(el)
						that.invoiceNum.push(el.nums)

					})
					this.invoiceNum = String(this.invoiceNum)
				}
				// console.log(typeof(this.invoiceNum))
				// console.log(this.invoiceNum)
				//第一次开发票
				if (this.status == 2) {
					if (this.ids == 1) {
						if (this.invoiceNum !== '') {
							if (this.surplusOil == 0) {
								uni.showModal({
									title: '提示',
									content: '确认开票',
									success: (res) => {
										if (res.confirm) {
											this.test.post('order/make_invoice', {
												id: this.id,
												invoice_type: this.typeInvoice,
												is_invoice: this.yesORno,
												invoice_split: this.invoiceNum,
												invoice_money: this.moeny
											}).then(res => {
												console.log(res)
												if (res.statusCode == 200 && res.data.errorCode == 0) {
													uni.showToast({
														title: '开票成功'
													})
													uni.redirectTo({
														url: '../orderDtails/orderDtails?id=' + this.id + '&no=' + this.no + '&status=' + this.status
													})
												} else {
													uni.showToast({
														title: res.data.message,
														icon: 'none'
													});
												}
											}).catch(err => {
												console.log(err)
											})
										} else {
											return;
										}
									}


								})

							} else {
								uni.showToast({
									title: '发票拆分数量必须与当前油量一致',
									icon: 'none'
								})
							}

						} else {
							uni.showToast({
								title: '请填写拆分数量',
								icon: 'none'
							})
						}

					} else if (this.ids == -1) {
						const that = this;
						uni.showModal({
							title: '提示',
							content: '确认开票',
							success: (res) => {
								console.log(res)
								if (res.confirm) {
									console.log(1)
									this.test.post('order/make_invoice', {
										id: this.id,
										invoice_type: this.typeInvoice,
										is_invoice: this.yesORno,
										invoice_split: this.currentOil,
										invoice_money: this.moeny
									}).then(res => {
										console.log(res)
										if (res.statusCode == 200 && res.data.errorCode == 0) {
											uni.showToast({
												title: '开票成功'
											})
											uni.redirectTo({
												url: '../orderDtails/orderDtails?id=' + this.id + '&no=' + this.no + '&status=' + this.status
											})
										} else {
											uni.showToast({
												title: res.data.message,
												icon: 'none'
											});
										}
									}).catch(err => {
										console.log(err)
									})
								} else {
									return;
								}
							}
						})
					}

				}else{//补开发票
					if (this.ids == 1) {
						if (this.invoiceNum !== '') {
							if (this.surplusOil == 0) {
								uni.showModal({
									title: '提示',
									content: '确认开票',
									success: (res) => {
										if (res.confirm) {
											this.test.post('order/repair_invoic', {
												id: this.id,
												invoice_type: this.typeInvoice,
												is_invoice: this.yesORno,
												invoice_split: this.invoiceNum,
												invoice_money: this.moeny
											}).then(res => {
												console.log(res)
												if (res.statusCode == 200 && res.data.errorCode == 0) {
													uni.showToast({
														title: '开票成功'
													})
													uni.redirectTo({
														url: '../orderDtails/orderDtails?id=' + this.id + '&no=' + this.no + '&status=' + this.status
													})
												} else {
													uni.showToast({
														title: res.data.message,
														icon: 'none'
													});
												}
											}).catch(err => {
												console.log(err)
											})
										} else {
											return;
										}
									}
					
					
								})
					
							} else {
								uni.showToast({
									title: '发票拆分数量必须与当前油量一致',
									icon: 'none'
								})
							}
					
						} else {
							uni.showToast({
								title: '请填写拆分数量',
								icon: 'none'
							})
						}
					
					} else if (this.ids == -1) {
						const that = this;
						uni.showModal({
							title: '提示',
							content: '确认开票',
							success: (res) => {
								console.log(res)
								if (res.confirm) {
									console.log(1)
									this.test.post('order/repair_invoic', {
										id: this.id,
										invoice_type: this.typeInvoice,
										is_invoice: this.yesORno,
										invoice_split: this.currentOil,
										invoice_money: this.moeny
									}).then(res => {
										console.log(res)
										if (res.statusCode == 200 && res.data.errorCode == 0) {
											uni.showToast({
												title: '开票成功'
											})
											uni.redirectTo({
												url: '../orderDtails/orderDtails?id=' + this.id + '&no=' + this.no + '&status=' + this.status
											})
										} else {
											uni.showToast({
												title: res.data.message,
												icon: 'none'
											});
										}
									}).catch(err => {
										console.log(err)
									})
								} else {
									return;
								}
							}
						})
					}
					
				}

			}
		}

	},
	components: {
		tButton,
		infoImg,
		infoText
	},
	watch: {
		// 监听用户是否输入数量超过购买总数
		surplusOil(newValue, oldValue) {
			console.log(newValue, oldValue)
			if (newValue < 0) {
				this.btn.disabled = true;
				uni.showToast({
					title: '输入的数字不能超过总吨数',
					icon: 'none'
				})
			} else {
				this.btn.disabled = false;
			}
		},
	},
	computed: {
		// 计算拆分发票油的数量
		surplusOil() {
			if (this.list.length === 0) {
				return parseFloat(this.currentOil - this.num)
			} else {
				let sum = parseFloat(this.currentOil - this.num)
				for (let i = 0; i < this.list.length; i++) {
					sum = sum - this.list[i].nums;
				}
				return sum
			}


		},

	}
}
